<template>
    <div>
        <el-header>
            <Header
                active-index=""></Header>
        </el-header>
        <el-main>
            <el-page-header @back="goBack" :content="caseData.patientName + '的病历详情'">
            </el-page-header>
            <el-tabs v-model="activeName" @tab-click="handleClick">
                <el-tab-pane label="患者信息" name="first">
                    <el-form :model="infoForm">
                        <el-form-item label="患者卡号" :label-width="formLabelWidth">
                            <el-input v-model="infoForm.patientId" autocomplete="off" disabled></el-input>
                        </el-form-item>
                        <el-form-item label="患者姓名" :label-width="formLabelWidth">
                            <el-input v-model="infoForm.patientName" autocomplete="off" disabled></el-input>
                        </el-form-item>
                        <el-form-item label="患者性别" :label-width="formLabelWidth">
                            <el-radio-group v-model="infoForm.gender" disabled>
                                <el-radio-button label="男性"></el-radio-button>
                                <el-radio-button label="女性"></el-radio-button>
                            </el-radio-group>
                        </el-form-item>
                        <el-form-item label="患者地址" :label-width="formLabelWidth">
                            <el-input v-model="infoForm.address" autocomplete="off" disabled></el-input>
                        </el-form-item>
                        <el-form-item label="出生日期" :label-width="formLabelWidth">
                            <el-input v-model="infoForm.birthday" autocomplete="off" disabled></el-input>
                        </el-form-item>
                        <el-form-item label="患者电话" :label-width="formLabelWidth">
                            <el-input v-model="infoForm.telephone" autocomplete="off" disabled></el-input>
                        </el-form-item>
                        <el-form-item label="患者单位" :label-width="formLabelWidth">
                            <el-input v-model="infoForm.unit" autocomplete="off" placeholder="未填写" disabled></el-input>
                        </el-form-item>
                        <el-form-item label="身份证号" :label-width="formLabelWidth">
                            <el-input v-model="infoForm.idcard" autocomplete="off" disabled></el-input>
                        </el-form-item>
                    </el-form>
                </el-tab-pane>
                <el-tab-pane label="检查项目" name="second">
                    <el-form :model="caseData">
                        <el-form-item label="左眼视力" :label-width="formLabelWidth">
                            <el-input v-model="caseData.eyesightOS" autocomplete="off" disabled></el-input>
                        </el-form-item>
                        <el-form-item label="右眼视力" :label-width="formLabelWidth">
                            <el-input v-model="caseData.eyesightOD" autocomplete="off" disabled></el-input>
                        </el-form-item>
                        <el-form-item label="左眼眼压" :label-width="formLabelWidth">
                            <el-input v-model="caseData.iopod" autocomplete="off" disabled></el-input>
                        </el-form-item>
                        <el-form-item label="右眼眼压" :label-width="formLabelWidth">
                            <el-input v-model="caseData.iopos" autocomplete="off" disabled></el-input>
                        </el-form-item>
                    </el-form>
                </el-tab-pane>
                <el-tab-pane label="IOL Master" name="third">
                    <el-form :model="caseData">
                        <el-form-item label="左眼眼轴" :label-width="formLabelWidth">
                            <el-input v-model="caseData.alos" autocomplete="off" disabled></el-input>
                        </el-form-item>
                        <el-form-item label="右眼眼轴" :label-width="formLabelWidth">
                            <el-input v-model="caseData.alod" autocomplete="off" disabled></el-input>
                        </el-form-item>
                    </el-form>
<!--                    <pdf-->
<!--                            style="width: 900px; height: 100%; float: right; margin-top: -160px;"-->
<!--                            ref="pdf"-->
<!--                            :src="iolmaster">-->
<!--                    </pdf>-->
<!--                                    <embed src="../assets/img/PDF/MMT-Full_4028.pdf" type="application/pdf" width="800" height="600"  />-->
<!--                    <iframe src="../assets/img/PDF/MMT-Full_4028.pdf" frameborder="0"></iframe>-->
<!--                    <iframe src="../assets/iwmg/login-bg1.jpg" frameborder="0"></iframe>-->
            </el-tab-pane>
                <el-tab-pane label="扫描激光眼底检查" name="fourth">扫描激光眼底检查</el-tab-pane>
                <el-tab-pane label="光学相干断层OCT" name="fifth">光学相干断层OCT</el-tab-pane>
            </el-tabs>
            <el-form class="diagnosis-result">
                <el-form-item label="诊断结论">
                    <el-select v-model="caseData.diagnosis" placeholder="请选择活动区域" :disabled="true">
                        <el-option label="视力正常" value="shanghai"></el-option>
                        <el-option label="普通近视" value="beijing"></el-option>
                        <el-option label="高度近视" value="beijing"></el-option>
                        <el-option label="病理性近视" value="beijing"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="治疗建议" placeholder="暂无">
                    <el-input v-model="caseData.recommend" :disabled="true"></el-input>
                </el-form-item>
                <el-form-item label="就诊时间">
                    {{caseData.checktime}}
                </el-form-item>
                <el-form-item label="就诊医生">
                    <el-select v-model="value" filterable placeholder="暂无" :disabled="true">
                        <el-option
                                v-for="item in options"
                                :key="item.value"
                                :label="item.label"
                                :value="item.value">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="复诊计划">
                    <el-select v-model="caseData.plan" placeholder="暂无" :disabled="true">
                        <el-option label="一周" value="shanghai"></el-option>
                        <el-option label="两周" value="beijing"></el-option>
                        <el-option label="一月" value="beijing"></el-option>
                        <el-option label="三月" value="beijing"></el-option>
                        <el-option label="半年" value="beijing"></el-option>
                        <el-option label="一年" value="beijing"></el-option>
                    </el-select>
                </el-form-item>
            </el-form>
            <pdf
                    v-show="activeName=='third'"
                    style="width: 900px; height: 100%; margin-top: -130px; margin-left: 900px;"
                    ref="pdf"
                    :src="iolmaster">
            </pdf>
        </el-main>
        <el-footer>爱尔眼科高度近视智能诊断系统(  推荐使用IE9+,Firefox、Chrome 浏览器访问   )</el-footer>
    </div>
</template>

<script>
    import Header from "../components/Header"
    import pdf from 'vue-pdf'
    export default {
        name: "PostCaseDetail",
        components: {Header, pdf},
        data() {
            return {
                activeName: 'first',
                id: '',
                caseData: {
                    diagnosis: '',
                    recommend: '',
                    checktime: '',
                    plan: ''
                },
                options: [{
                    value: '选项1',
                    label: '黄金糕'
                }, {
                    value: '选项2',
                    label: '双皮奶'
                }, {
                    value: '选项3',
                    label: '蚵仔煎'
                }, {
                    value: '选项4',
                    label: '龙须面'
                }, {
                    value: '选项5',
                    label: '北京烤鸭'
                }],
                value: '',
                infoForm: {
                    id: '',
                    patientName: '',
                    patientId: '',
                    gender: '',
                    birthday: '',
                    telephone: '',
                    address: '',
                    unit: '',
                    idcard: '',
                },
                formLabelWidth: '120px',
                iolmaster: '',
            }
        },
        created() {
            this.id = this.$route.params.id
            const _this = this
            _this.$axios.get("/caselist/pastCase/" + _this.id, {
                headers: {
                    "Authorization": _this.$store.getters.getToken
                }
            }).then(res => {
                _this.caseData = res.data.data;
                //          患者基本信息
                console.log(res.data.data)
                // console.log(_this.caseData)
                _this.$axios.get("/patient/patientIndex/" + res.data.data.patientId, {
                    headers: {
                        "Authorization": _this.$store.getters.getToken
                    }
                }).then(res1 => {
                    _this.infoForm = res1.data.data
                })
                // console.log(_this.caseData.iolmaster)
                if(res.data.data.iolmaster != null) {
                    _this.$axios.get("/examdetail/getByExamId/" + _this.caseData.iolmaster, {
                        headers: {
                            "Authorization": _this.$store.getters.getToken
                        }
                    }).then(res2 => {
                        console.log(res2)
                        _this.iolmaster = res2.data.data
                        console.log(_this.iolmaster)
                    })
                }
            })

        },
        methods: {
            handleClick(tab, event) {
                // console.log(tab, event);
            },
            goBack() {
                this.$router.go(-1);
            }
        }
    }
</script>

<style scoped>
    .el-header {
        width: 100%;
        position:absolute;
        left: 0;
        padding: 0;
        background-color: #B3C0D1;
        color: #333;
        text-align: center;
        line-height: 65px;
        top: 0;
    }
    .el-main {
        width: 100%;
        margin-top: 60px;
        background-color: #E9EEF3;
        color: #333;
        text-align: center;
        height: 850px;
    }
    .el-footer {
        bottom: 0;
        width: 100%;
        position:fixed;
        left: 0;
        padding: 0;
        background-color: #B3C0D1;
        color: #333;
        text-align: center;
        line-height: 65px;
        font-size: 10px;
    }
    .el-form {
        width: 400px;
    }
    .el-main .el-form .el-form-item .el-input {
        width: 210px;
    }
    .el-page-header {
        margin-bottom: 20px;
    }
     .el-tabs /deep/ .el-tabs__item{
        font-size: 14px;
    }
    .el-main .diagnosis-result {
        position: absolute;
        /*margin-top: -450px;*/
        /*margin-left: 500px;*/
        top: 175px;
        left: 500px;
    }
</style>