<template>
    <div>
        <el-header>
            <Header
                    active-index=""></Header>
        </el-header>
        <el-main>
            <el-page-header @back="goBack" :content="caseData.patientName + '的病历详情'">
            </el-page-header>
            <el-tabs v-model="activeName" @tab-click="handleClick">
                <el-tab-pane label="患者信息" name="first">
                    <el-form :model="infoForm">
                        <el-form-item label="患者卡号" :label-width="formLabelWidth">
                            <el-input v-model="infoForm.patientId" autocomplete="off" disabled></el-input>
                        </el-form-item>
                        <el-form-item label="患者姓名" :label-width="formLabelWidth">
                            <el-input v-model="infoForm.patientName" autocomplete="off" disabled></el-input>
                        </el-form-item>
                        <el-form-item label="患者性别" :label-width="formLabelWidth">
                            <el-radio-group v-model="infoForm.gender" disabled>
                                <el-radio-button label="男性"></el-radio-button>
                                <el-radio-button label="女性"></el-radio-button>
                            </el-radio-group>
                        </el-form-item>
                        <el-form-item label="患者地址" :label-width="formLabelWidth">
                            <el-input v-model="infoForm.address" autocomplete="off" disabled></el-input>
                        </el-form-item>
                        <el-form-item label="出生日期" :label-width="formLabelWidth">
                            <el-input v-model="infoForm.birthday" autocomplete="off" disabled></el-input>
                        </el-form-item>
                        <el-form-item label="患者电话" :label-width="formLabelWidth">
                            <el-input v-model="infoForm.telephone" autocomplete="off" disabled></el-input>
                        </el-form-item>
                        <el-form-item label="患者单位" :label-width="formLabelWidth">
                            <el-input v-model="infoForm.unit" autocomplete="off" placeholder="未填写" disabled></el-input>
                        </el-form-item>
                        <el-form-item label="身份证号" :label-width="formLabelWidth">
                            <el-input v-model="infoForm.idcard" autocomplete="off" disabled></el-input>
                        </el-form-item>
                    </el-form>
                </el-tab-pane>
                <el-tab-pane label="检查项目" name="second">
                    <el-form :model="caseData">
                        <el-form-item label="左眼视力" :label-width="formLabelWidth">
                            <el-input v-model="caseData.eyesightOS" autocomplete="off"></el-input>
                        </el-form-item>
                        <el-form-item label="右眼视力" :label-width="formLabelWidth">
                            <el-input v-model="caseData.eyesightOD" autocomplete="off"></el-input>
                        </el-form-item>
                        <el-form-item label="左眼眼压" :label-width="formLabelWidth">
                            <el-input v-model="caseData.iopod" autocomplete="off"></el-input>
                        </el-form-item>
                        <el-form-item label="右眼眼压" :label-width="formLabelWidth">
                            <el-input v-model="caseData.iopos" autocomplete="off"></el-input>
                        </el-form-item>
                    </el-form>
                </el-tab-pane>
                <el-tab-pane label="IOL Master" name="third">
                    <el-form :model="caseData">
                        <el-form-item label="左眼眼轴" :label-width="formLabelWidth">
                            <el-input v-model="caseData.alos" autocomplete="off"></el-input>
                        </el-form-item>
                        <el-form-item label="右眼眼轴" :label-width="formLabelWidth">
                            <el-input v-model="caseData.alod" autocomplete="off"></el-input>
                        </el-form-item>
                    </el-form>
<!--                    <div v-if="iol">-->
<!--                        <pdf-->
<!--                            style="width: 1000px; height: 100%"-->
<!--                            ref="pdf"-->
<!--                            :src="iolmaster">-->
<!--                        </pdf>-->
<!--                    </div>-->
<!--                    <div v-else>-->
<!--                        <el-upload-->
<!--                                class="upload-demo"-->
<!--                                drag-->
<!--                                action-->
<!--                                :http-request="fileUpload">-->
<!--                            <i class="el-icon-upload"></i>-->
<!--                            <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>-->
<!--                            <div class="el-upload__tip" slot="tip">只能上传jpg/png文件，且不超过500kb</div>-->
<!--                        </el-upload>-->
<!--                    </div>-->
                </el-tab-pane>
                <el-tab-pane label="扫描激光眼底检查" name="fourth">扫描激光眼底检查</el-tab-pane>
                <el-tab-pane label="光学相干断层OCT" name="fifth">光学相干断层OCT</el-tab-pane>
            </el-tabs>
            <el-form class="diagnosis-result">
                <el-form-item label="诊断结论">
                    <el-select v-model="caseData.diagnosis" placeholder="请选择诊断结论">
                        <el-option label="视力正常" value="视力正常"></el-option>
                        <el-option label="普通近视" value="普通近视"></el-option>
                        <el-option label="高度近视" value="高度近视"></el-option>
                        <el-option label="病理性近视" value="病理性近视"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="治疗建议" placeholder="暂无">
                    <el-input v-model="caseData.recommend"></el-input>
                </el-form-item>
                <el-form-item label="就诊时间">
                    {{caseData.checktime}}
                </el-form-item>
                <el-form-item label="就诊医生">
                    <el-select v-model="value" filterable placeholder="暂无" >
                        <el-option
                                v-for="item in options"
                                :key="item.value"
                                :label="item.label"
                                :value="item.value">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="复诊计划">
                    <el-select v-model="caseData.plan" placeholder="暂无" >
                        <el-option label="一周" value="7"></el-option>
                        <el-option label="两周" value="14"></el-option>
                        <el-option label="一月" value="30"></el-option>
                        <el-option label="三月" value="90"></el-option>
                        <el-option label="半年" value="180"></el-option>
                        <el-option label="一年" value="365"></el-option>
                    </el-select>
                </el-form-item>
                <el-button type="primary" icon="el-icon-check" @click="submitCaseData">修改完成</el-button>
            </el-form>
            <div v-show="activeName=='third'">
                <div v-if="iol">
                    <pdf
                            style="width: 900px; height: 100%; margin-top: -130px; margin-left: 900px;"
                            ref="pdf"
                            :src="iolmaster">
                    </pdf>
                </div>
                <div v-else>
                    <el-upload
                            class="upload-demo"
                            drag
                            action
                            :http-request="fileUpload">
                        <i class="el-icon-upload"></i>
                        <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
                        <div class="el-upload__tip" slot="tip">只能上传jpg/png文件，且不超过500kb</div>
                    </el-upload>
                </div>
            </div>
        </el-main>
        <el-footer>爱尔眼科高度近视智能诊断系统(  推荐使用IE9+,Firefox、Chrome 浏览器访问   )</el-footer>
    </div>
</template>

<script>
    import Header from "../components/Header"
    import pdf from 'vue-pdf'

    export default {
        name: "CaseDetail",
        components: {Header, pdf},
        inject: ['reload'],
        data() {
            return {
                activeName: 'first',
                id: '',
                caseData: {
                    diagnosis: '',
                    recommend: '',
                    checktime: '',
                    plan: '',
                    iolmaster: ''
                },
                options: [{
                    value: '选项1',
                    label: '秦小林'
                }, {
                    value: '选项2',
                    label: '胡建斌'
                }, {
                    value: '选项3',
                    label: '王杰'
                }],
                value: '',
                infoForm: {
                    id: '',
                    patientName: '',
                    patientId: '',
                    gender: '',
                    birthday: '',
                    telephone: '',
                    address: '',
                    unit: '',
                    idcard: '',
                },
                formLabelWidth: '120px',
                iolmaster: '',
                iol: false,

            }
        },
        created() {
            this.id = this.$route.params.id
            const _this = this
            _this.$axios.get("/caselist/pastCase/" + _this.id, {
                headers: {
                    "Authorization": _this.$store.getters.getToken
                }
            }).then(res => {
                _this.caseData = res.data.data;
                // console.log(res.data.data);
                //          患者基本信息
                _this.$axios.get("/patient/patientIndex/" + res.data.data.patientId, {
                    headers: {
                        "Authorization": _this.$store.getters.getToken
                    }
                }).then(res1 => {
                    _this.infoForm = res1.data.data
                })
                // console.log(res.data.data.iolmaster)
                if(res.data.data.iolmaster != null && res.data.data.iolmaster != "") {
                    _this.iol = true;
                    _this.$axios.get("/examdetail/getByExamId/" + _this.caseData.iolmaster, {
                        headers: {
                            "Authorization": _this.$store.getters.getToken
                        }
                    }).then(res2 => {
                        console.log(res2)
                        _this.iolmaster = res2.data.data
                        console.log(_this.iolmaster)

                    })
                }
            })

        },
        methods: {
            handleClick(tab, event) {
                // console.log(tab, event);
            },
            goBack() {
                this.$router.go(-1);
            },
            fileUpload(file) {
                const _this = this
                console.log(file)
                const data = new FormData();
                data.append('file', file.file);
                _this.caseData.iolmaster = file.file.name
                console.log(_this.caseData)
                _this.$axios.post('/examdetail/uploadIOLMaster', data, {
                    headers: {
                        'Content-Type': 'multipart/form-data',
                    },
                }).then(res => {
                    console.log(res);
                    if(res.status === 200) {

                        _this.iolmaster = res.data.data.filepath
                        _this.caseData.iolmaster = res.data.data.examid
                        // _this.caseData.iolmaster = _this.iolmaster
                        _this.iol = true;
                        _this.caseData.alod = res.data.data.alod
                        _this.caseData.alos = res.data.data.alos
                    }
                }).catch(err => {
                    console.log(err);
                });
            },
            submitCaseData() {
                const _this = this
                _this.$axios.post('caselist/submitCase', _this.caseData, {
                    headers: {
                        'Authorization': _this.$store.getters.getToken,
                    },
                }).then(res => {
                    console.log(res);
                    if(res.data.code === 200) {
                        _this.$message({
                            type: 'success',
                            message: '完善病历成功!'
                        });
                        this.$router.go(-1);
                    }
                }).catch(err => {
                    console.log(err);
                    _this.$message.error('操作失败');
                });
            }
        }
    }
</script>

<style scoped>
    .el-header {
        width: 100%;
        position:absolute;
        left: 0;
        padding: 0;
        background-color: #B3C0D1;
        color: #333;
        text-align: center;
        line-height: 65px;
        top: 0;
    }
    .el-main {
        width: 100%;
        margin-top: 60px;
        background-color: #E9EEF3;
        color: #333;
        text-align: center;
        height: 840px;
    }
    .el-footer {
        bottom: 0;
        width: 100%;
        position:fixed;
        left: 0;
        padding: 0;
        background-color: #B3C0D1;
        color: #333;
        text-align: center;
        line-height: 65px;
        font-size: 10px;
    }
    .el-form {
        width: 400px;
    }
    .el-main .el-form .el-form-item .el-input {
        width: 210px;
    }
    .el-page-header {
        margin-bottom: 20px;
    }
    .el-tab-pane {
        font-size: 24px;
    }
    .el-main .diagnosis-result {
        position: absolute;
        /*margin-top: -450px;*/
        /*margin-left: 500px;*/
        top: 175px;
        left: 500px;
    }
    .upload-demo {
        width: 900px;
        height: 100%;
        margin-top: -130px;
        margin-left: 900px;
    }
</style>